import React, { Component } from 'react';
import { Layout, Menu, Button, Drawer, Divider, Affix, Card } from 'antd';
import ReactSVG from 'react-svg';
import Link from 'next/link';


class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileMenu: false,
      fixed: false,
      loggedIn: false
    }
  }

  render() {
    let { fixed, loggedIn } = this.state;
    return (
      <Affix offsetTop={0} onChange={affixed => console.log(affixed)}>
        <Layout.Header className={`${fixed ? 'fixed' : ''}`} >
          <div className="container">
            <Link href="/">
              <a>
                <ReactSVG svgClassName="d-flex align-center" src="/static/img/logo/logo-dark.svg" />
              </a>
            </Link>
            <Menu
              theme="light"
              mode="horizontal"
              defaultSelectedKeys={['Features']}
            >
              <Menu.Item key="Features"><Link href="/features"><a>Features </a></Link></Menu.Item>
              <Menu.Item key="Integrations"><Link href="/integrations"><a>Integrations </a></Link></Menu.Item>
              <Menu.Item key="Solutions"><Link href="/solutions"><a>Solutions </a></Link></Menu.Item>
              <Menu.Item key="Pricing"><Link href="/pricing"><a>Pricing </a></Link></Menu.Item>
              <Menu.Item key="Blog"><Link href="/blog"><a>Blog</a></Link></Menu.Item>
            </Menu>
            <div>
              {
                !loggedIn ?
                <React.Fragment>
                  <Button type="primary">
                    Start free trial
                    <ReactSVG src="/static/img/icon/right.svg" />
                  </Button>
                  <Button className="btn-link ml-24 mp-24 p-8 text-thick" onClick={() => this.setState({ loggedIn: true })}>
                    Sign in
                  </Button>
                </React.Fragment>
                :
                <React.Fragment>
                  <Card.Meta 
                    avatar={"13"}
                    title="days left in trial period"
                  />
                </React.Fragment>
              }


            </div>
          </div>


          <div className="mobile-header">
            <ReactSVG svgClassName="d-flex align-center" src="/static/img/logo/logo-dark.svg" />
            <ReactSVG onClick={() => this.setState({ mobileMenu: true })} className="svg-wrapper" svgClassName="d-flex align-center" src="/static/img/icon/menu.svg" />
            <Drawer
              title={<div className="mobile-header">
                <ReactSVG svgClassName="d-flex align-center" src="/static/img/logo/logo-dark.svg" />
                <ReactSVG onClick={() => this.setState({ mobileMenu: false })} className="svg-wrapper" svgClassName="d-flex align-center" src="/static/img/icon/close.svg" />
              </div>
              }
              placement="right"
              closable={false}
              onClose={() => this.setState({ mobileMenu: false })}
              visible={this.state.mobileMenu}
            >
              <div className="content text-right">
                <div>
                  <Button type="primary">
                    Start free trial
              <ReactSVG src="/static/img/icon/right.svg" />
                  </Button>
                </div>
                <div className="mt-16">
                  <Button className="btn-link bg-none text-thick">
                    Sign in
            </Button>
                </div>
                <Divider />
                <h3>
                  <Link href="/features">
                    <a>
                      Features
                   </a>
                  </Link>
                </h3>
                <h3>
                  <Link href="/integrations">
                    <a>
                      Integrations
                  </a>
                  </Link>
                </h3>
                <h3>
                  <Link href="/solutions">
                    <a>
                      Solutions
                  </a>
                  </Link>
                </h3>
                <h3>
                  <Link href="/pricing">
                    <a>
                      Pricing
                  </a>
                  </Link>
                </h3>
                <h3>
                  <Link href="/blog">
                    <a>
                      Blog
                  </a>
                  </Link>
                </h3>
              </div>
            </Drawer>
          </div>


        </Layout.Header>
      </Affix>
    );
  };
};

export default Header;